/*
ajax
: promise(: response.data)
 */
import axios from 'axios'
import store from '@/store'
import Qs from 'qs'
import {CheckNotLogin} from "../tools"
import {checkCanRefresh, refreshToken} from "../tools/sso";

// 
axios.defaults.withCredentials = true;

// axios  401 
axios.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          //  tokenString  3  RefreshToken, 3 
          if (checkCanRefresh()) {
            refreshToken(store, function () {
              if (!CheckNotLogin()) {
                window.location.href = "/sso/login/?redirectUrl=" + window.location.href;
              }
            });
          } else {
            if (!CheckNotLogin()) {
              window.location.href = "/sso/login/?redirectUrl=" + window.location.href;
            }
          }
      }
    }
    return Promise.reject(error.response.data)   // 
  }
);

export const ajax = function (url, data = {}, type = 'GET') {
  return new Promise(function (resolve, reject) {
    // ajax
    let promise;
    if (type === 'GET') {
      // url query
      let dataStr = ''; //
      Object.keys(data).forEach(key => {
        dataStr += key + '=' + data[key] + '&'
      });
      if (dataStr !== '') {
        dataStr = dataStr.substring(0, dataStr.lastIndexOf('&'));
        url = url + '?' + dataStr
      }
      // get, promise 
      promise = axios.get(url,{headers: {'clientType': 'web',}})
    } else {
      // post, promise 
      //  Qs.stringify  axios  post ,
      promise = axios.post(url, Qs.stringify(data),{headers: {'clientType': 'web',}})
    }
    promise.then(function (response) {
      // resolve()
      resolve(response.data)
    }).catch(function (error) {
      //reject()
      reject(error)
    })
  })
};

export const download = function (url, data = {}, type = 'GET') {
  let dataStr = ''; //
  Object.keys(data).forEach(key => {
    dataStr += key + '=' + data[key] + '&'
  });
  if (dataStr !== '') {
    dataStr = dataStr.substring(0, dataStr.lastIndexOf('&'));
    url = url + '?' + dataStr
  }
  // get
  window.location.href = url;
};




